import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ThongTinCoBanService } from './thong-tin-co-ban.service';
import { DoiMatKhauComponent } from '../../../../shared/pages/doi-mat-khau/doi-mat-khau.component';
import { UserModel } from 'src/app/shared/models/user.model';
import { CapNhatThongTinComponent } from './cap-nhat-thong-tin/cap-nhat-thong-tin.component';
import { eMenuClient, environment } from 'src/environments/environment';
import { MainNavComponent } from '../main.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

@Component({
  selector: 'app-thong-tin-co-ban',
  templateUrl: './thong-tin-co-ban.component.html',
  styleUrls: ['./thong-tin-co-ban.component.scss'],
})
export class ThongTinCoBanComponent implements OnInit {
  _today = new Date();
  _length = 0;

  constructor(
    public __dialog: MatDialog,
    private __service: ThongTinCoBanService,
    private __mainComponent: MainNavComponent,
    private __snackbar: MatSnackBar,
    private __router: Router
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.CLIENT,
      eMenuClient[eMenuClient.THONG_TIN_CO_BAN]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.CLIENT
    );
  }
  __user = new UserModel('', '', '', '', '', '', '');
  showProgressBar = false;
  ngOnInit(): void {
    this.showProgressBar = true;
    this.getDataUser();
  }
  delay(ms: number) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  private getDataUser() {
    this.showProgressBar = true;
    this.__service.getUser().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this.__user = data;
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
  update() {
    const dialogRef = this.__dialog.open(CapNhatThongTinComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        user: this.__user,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
      }
    });
  }
  changePassword() {
    const dialogRef = this.__dialog.open(DoiMatKhauComponent, {
      disableClose: true,
      width: '20rem',
      data: {
        user: this.__user,
        isClient: true
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
      }
    });
  }
}
